:root{
  --bg-white: #F8F8F8;
  --gray-one: #F5F5F5;
  --gray-two: #CDC8C8 ;  
  --gray-three: #7C7B7B  ; 
  
 
  /* var(--accent-color); */
  --darkColor: #101315 ;
  --medColor: #344966 ;
  --mainColor: #B4CDED;
  --accentColor: #a1b953 ;
  --whiteColor: #F0F4EF;

  font-size: 14px;
 
  background-color: #FFFFFF;
  
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
